/* comp/Resume CSS */
.resume-table {
  width: 100%;
  height: 500px;
  background-color: rgb(255, 235, 235);
}

.resume-image {
  display: block; 
  margin: 0 auto; 
  width: 200px; 
  height: 250px; 
}

.resume-h1{
    text-align: center;
}







/* comp/Projects CSS */
@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.projects-image {
  width: 70px; 
  margin-left: 20px;
  margin-top : 20px;
}




/* comp/Awards CSS */
.awards-container {
  display: flex;
  align-items: center;
  margin-bottom: 20px;
}

.awards-image {
  width: 200px; 
  margin-right: 20px; 
  animation: fadeIn 1.5s ease-in;
  margin-top : 20px;
}

.awards-details {
  flex: 1; 
  margin-right: 20px; 
}





/* buttons css */
button{
  width: 130px;
  height: 40px;
  color: #fff;
  border-radius: 5px;
  padding: 10px 25px;
  font-family : 'Franklin Gothic Medium', sans-serif;
  font-weight: 500;
  background: rgb(246, 100, 163);
  background: radial-gradient(circle, rgb(249, 116, 174) 0%, rgba(118,174,241,1) 100%);
  line-height: 42px;
  padding: 0;
  border: none;
  cursor: pointer;
  transition: all 0.3s ease;
  position: relative;
  display: block;
   box-shadow:inset 2px 2px 2px 0px rgba(255,255,255,.5),
   7px 7px 20px 0px rgba(0,0,0,.1),
   4px 4px 5px 0px rgba(0,0,0,.1);
  outline: none;
  margin: 20px;
  

}

button:before,
button:after {
  position: absolute;
  content: "";
  height: 0%;
  width: 1px;
 box-shadow:
   -1px -1px 20px 0px rgb(167, 3, 140),
   -4px -4px 5px 0px rgb(167, 3, 140),
   7px 7px 20px 0px rgba(0,0,0,.4),
   4px 4px 5px 0px rgba(0,0,0,.3);
}
button:before {
  right: 0;
  top: 0;
  transition: all 500ms ease;
}
button:after {
  left: 0;
  bottom: 0;
  transition: all 500ms ease;
}
button:hover{
  background: transparent;
  color: #76aef1;
  box-shadow: none;
}
button:hover:before {
  transition: all 500ms ease;
  height: 100%;
}
button:hover:after {
  transition: all 500ms ease;
  height: 100%;
}




